.contact-form {
    padding-bottom: 120px;

    @include tablet {
        margin-top: 54px;
        padding-bottom: 80px;
    }

    @include mobile-final {
        padding-bottom: 65px;
    }

    &__wrapper {
        border-radius: 15px;
        padding: 30px 20px;
        background-image: url('../../img/form/form-bg.png');
        background-size: cover;
        background-repeat: no-repeat;
        background-color: black;
        border: 1px solid var(--osnovnoy-hover);

        @include tablet {
            padding: 22px 21px 20px 20px;
        }

        @include mobile-final {
            padding: 16px;
        }
    }

    &__title {
        margin-bottom: 35px;
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 48px;
        line-height: 1.3;
        letter-spacing: -1.5px;
        color: var(--tekst-zagolovok);

        @include tablet {
            width: 100%;
            max-width: 332px;
            font-size: 32px;
        }

        @include mobile-final {
            margin-bottom: 14px;
            font-size: 20px;
        }
    }
}

.form {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 26px;

        @include tablet {
            gap: 10px;
        }

        @include mobile {
            width: 100%;
        }

        @include pre-mobile {
            align-items: flex-start;
            margin-top: 0;
            margin-bottom: 8px;
            width: 100%;
            gap: 6px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 15px;

        @include tablet {
            gap: 5px;
        }

        @include pre-mobile {
            width: 100%;
            gap: 0;
        }
    }

    &__label {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 20px;
        color: var(--tekst-zagolovok);

        @include tablet {
            font-size: 18px;
            letter-spacing: -0.6px;
        }

        @include mobile-final {
            font-size: 14px;
        }
    }

    &__input {
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 10px;
        padding: 20px 16px;
        width: 330px;
        height: 59px;
        background-color: transparent;
        transition: all 0.3s ease-in-out;

        @include tablet {
            height: 42px;
        }

        @include pre-mobile {
            width: 100%;
        }

        @include mobile-final {
            padding: 6px 10px;
            height: auto;
        }

        @include hover {
            border: 1px solid var(--color---white);
        }

        &::placeholder {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 0.05em;
            color: var(--tekst-zagolovok);

            @include small-tablet {
                font-size: 14px;
            }

            @include mobile-final {
                font-size: 14px;
                line-height: 125%;
                letter-spacing: -0.9px;
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        gap: 28px;

        @include tablet {
            gap: 2px;
        }

        @include pre-mobile {
            align-items: flex-start;
        }
    }

    &__textAreaBox {
        width: 100%;
        height: 163px;
        max-width: 738px;

        @include tablet {
            height: auto;
            max-width: 312px;
        }

        @include pre-mobile {
            width: 100%;
            max-width: 100%;
        }

        @include mobile {
            width: 100%;
        }
    }

    &__button {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--tekst-zagolovok);
        border-radius: 10px;
        padding: 13px 85px;
        height: 72px;
        font-family: inherit;
        font-weight: 500;
        font-size: 30px;
        line-height: 150%;
        color: var(--tekst-zagolovok);
        background-color: transparent;
        transition: background-color 0.3s ease-in-out;
        cursor: pointer;
        pointer-events: auto;

        @include tablet {
            margin-top: 34px;
            height: 59px;
            font-size: 20px;
        }

        @include mobile {
            width: 100%;
        }

        @include mobile-final {
            margin-top: 0;
            padding: 10px 65px;
            width: auto;
            height: auto;
            font-size: 16px;
        }

        &:focus {
            outline: 1px solid var(--color---white);
        }

        @include hover {
            color: var(--osnovnoy-hover);
            background-color: var(--tekst-zagolovok);
        }
    }

    &__checkbox {
        border: 1px solid var(--tekst-zagolovok);
        border-radius: 2px;
        width: 12px;
        height: 12px;
    }

    &__policy {
        padding-left: 20px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        color: var(--tekst-zagolovok);
        transition: color 0.3s ease-in;

        @include tablet {
            letter-spacing: 0;
        }
    }

    &__policy-link {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        text-decoration: underline;
        color: var(--tekst-zagolovok);
        transition: color 0.3s ease-in;

        &:focus {
            outline: 1px solid var(--color---white);
        }

        @include tablet {
            letter-spacing: 0;
        }

        @include hover {
            color: rgba(255, 255, 255, 0.5);
        }
    }

    &__consent {
        position: relative;

        @include pre-mobile {
            align-items: flex-start;
            margin-bottom: 10px;
        }
    }

    &__end {
        justify-content: flex-start;
        width: 330px;
        gap: 0;

        @include tablet {
            flex-direction: column-reverse;
            width: 312px;
            gap: 5px;
        }

        @include pre-mobile {
            align-items: flex-start;
            margin-top: 5px;
            width: 100%;
        }
    }
}

.select {
    position: relative;
    width: 330px;
    height: 59px;

    @include tablet {
        width: 330px;
        height: auto;
    }

    @include pre-mobile {
        width: 100%;
    }

    &__name {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;

        @include small-tablet {
            font-size: 14px;
        }
    }

    &__selected {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 10px;
        padding: 16px;
        width: auto;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-zagolovok);
        background-color: transparent;
        transition: border-color 0.3s ease-in-out;
        cursor: pointer;

        @include tablet {
            padding: 7px 16px;
        }

        @include mobile-final {
            padding: 3px 6px;
            font-size: 14px;
            letter-spacing: 0;
        }

        @include hover {
            border-color: var(--color---white);
        }

        &:focus-visible {
            outline: none !important;
            box-shadow: 0 0 0 2px var(--color---white);
        }
    }

    &__options {
        position: absolute;
        left: 0;
        top: calc(100% + 6px);
        z-index: 100;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        border-radius: 14px;
        padding: 16px;
        width: 100%;
        color: #555;
        background: #fff;
        opacity: 0;
        transform: translateY(-5px);
        transition: opacity 0.25s ease, transform 0.25s ease;
        pointer-events: none;
        gap: 10px;

        @include small-tablet {
            padding: 10px 16px;
        }

        &:focus-visible {
            outline: none !important;
            box-shadow: 0 0 0 2px var(--color---white);
        }
    }

    &__option {
        border-radius: 5px;
        padding: 10px 16px;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.5;
        letter-spacing: 0.05em;
        color: #7d7d7d;
        cursor: pointer;

        @include small-tablet {
            padding: 5px;
            font-size: 14px;
        }

        &:focus-visible {
            outline: none;
            box-shadow: 0 0 0 2px var(--color---white);
        }
    }

    &__arrowBox {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;

        @include mobile-final {
            margin-right: 5px;
        }
    }

    &__arrow {
        transition: transform 0.25s ease;
    }
}

.select.open .select__options {
    opacity: 1;
    transform: translateY(0);
    pointer-events: all;
}

.row-close {
    display: block;
    margin-top: -4px;
    margin-left: 19px;

    @include mobile-final {
        display: inline;
        margin-top: 0;
        margin-left: 0;
    }
}
