.reviews {

    &__title {
        margin-bottom: 32px;

        @include tablet {
            margin-bottom: 75px;
            font-size: 32px;
        }

        @include slider {
            margin-bottom: 5px;
        }

        @include mobile-final {
            margin-bottom: 24px;
            font-size: 20px;
            letter-spacing: -1px;
        }
    }

    &__slider-wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__inner {
        position: relative;
    }

    &__slider-controls {
        position: absolute;
        right: 0;
        top: 88px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 82px;
        gap: 10px;

    }
}

.review-card {
    display: flex;
    width: 1200px;
    gap: 32px;

    @include for-desktop-huge {
        justify-content: center;
        margin: 0 auto;
        width: 100%;
        max-width: 1200px;
    }

    @include slider {
        flex-direction: column;
        gap: 20px;
    }
    @include pre-mobile {
        gap: 5px;
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border: 3px solid var(--color---light-blue-12);
        border-radius: 15px;
        padding: 32px;
        width: 500px;

        @include for-desktop-huge {
            width: 300px;
            height: auto;
        }

        @include slider {
            border: none;
            padding: 0;
            width: auto;
        }


    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 60px;
        gap: 13px;

        @include slider {
            margin-bottom: 0;
            margin-bottom: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 0;
            background: var(--color---light-blue-12);
        }

        @include mobile-small {
            border-radius: 15px;
            padding: 8px 16px;
            width: 100%;
        }

        @include mobile-final {
            margin-bottom: 10px;
        }
    }

    &__name,
    &__age {
        font-weight: 700;
        font-size: 36px;
        line-height: 150%;
        letter-spacing: -0.8px;
        color: var(--osnovnoy-hover);

        @include tablet {
            font-size: 32px;
        }

        @include slider {
            font-size: 25px;
        }

        @include mobile-final {
            font-size: 20px;
            letter-spacing: -1px;
        }

    }

    &__icon {
        flex-shrink: 0;
        overflow: hidden;
        border-radius: 5px;
        width: 64px;
        height: 64px;
        background-size: cover;
    }

    &__text {
        overflow: hidden;
        margin-bottom: 16px;
        max-height: 162px;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: var(--tekst-abzac);
        transition: max-height 0.3s ease;

        @include tablet {
            font-size: 16px;
            line-height: 120%;
            margin-bottom: 8px;
        }

        @include mobile-small {
            border-radius: 15px;
            padding: 8px 16px;
            width: 100%;
        }
    }

    &__more {
        display: inline-flex;
        width: fit-content;
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        text-decoration: underline;
        color: var(--tekst-abzac);
        transition: color .3s ease-in-out;
        cursor: pointer;
        text-decoration-skip-ink: none;

        @include hover {
            color: var(--tekst-zagolovok);
        }
    }

    &__photo {
        display: flex;
        flex-direction: column;
        border-radius: 15px;
        border: 3px solid var(--color---light-blue-12);
        padding: 10px 15px;

        @include slider {
            padding: 10px;
        }
    }

    &__image {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        border-radius: 15px;
        width: 200px;
        height: 200px;

        @include slider {
            display: none;
        }
    }

}

.reviews__slider-prev,
.reviews__slider-next {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--osnovnoy-hover);
    border-radius: 10px;
    width: 36px;
    height: 36px;
    transition: background-color .3s ease-in-out;

    @include hover {
        background-color: var(--osnovnoy-hover);
    }
}

.reviews__slider-slide {
    display: flex;
    justify-content: center;
}
.reviews__slider-controls {
    top: 16px;

    @include slider {
        top: 5px
    }
}

.review__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}
.reviews__buttons {
    display: flex;
    flex-wrap: wrap;
    margin: 20px auto;
    gap: 30px;
    justify-content: center;
    justify-content: space-between;

    @include tablet {
        justify-content: center;
    }
}
.reviews__button {
    display: inline-block;
    width: 160px;
    color: var(--color---white);
    cursor: pointer;
    padding: 10px 12px;
    border-radius: 5px;
    background-color: var(--fon-charcoal);
    border: 1px solid var(--color---white);
    transition: all .3s ease-in;
    text-align: center;
    &:hover {
        background-color: var(--osnovnoy-hover);
    }

    @include tablet {
        padding: 5px;
        width: 120px;
        font-size: 12px;
    }
}
.reviews-list__title {
    color: var(--color---white);
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 10px;
}
