.header {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  margin-top: 25px;
  background-color: transparent;
  transition: all .5s ease-in;

  @include tablet {
    margin-top: 0;
    padding-top: 17px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 38px;

    @include for-desktop {
      height: auto;
    }
  }

  &__logo {
    font-weight: 400;
    font-size: 24px;
    line-height: 160%;
    letter-spacing: -0.03em;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 2px;
    color: #fff;

    @include for-desktop {
      font-size: 24px;
    }

    @include tablet {
      font-size: 16px;
    }
  }
}

.header.fixed {
  position: absolute;
  z-index: 100;
  padding: 10px 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
}
