.intro {
  z-index: 0;
  width: 100%;
  height: 100vh;
  background-image: url('../../img/photos/intro-bg.png');
  background-repeat: no-repeat;
  background-position: top center;

  @include tablet {
  }

  @include mobile {
    padding-bottom: 32px;
    height: auto;
  }

  @include mobile-final {
    height: auto;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 100px;
    width: 100%;

    @include tablet {
      padding-top: 82px;
    }

    @include mobile {
      padding-top: 90px;
    }

    @include mobile-final {
      padding-top: 64px;
      height: auto;
    }
  }

  &__title {
    display: inline-block;
    margin: 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 48px;
    line-height: 1.24;
    letter-spacing: -1.8px;
    color: #fff;
    text-align: center;

    @include for-desktop-large {
      width: 100%;
      max-width: 650px;
      font-size: 36px;
    }

    @include tablet {
      max-width: 566px;
      font-size: 32px;
      line-height: 1.28;
      letter-spacing: -1.5px;
    }

    @include mobile-final {
      font-size: 20px;
      letter-spacing: -0.6px;
    }
  }
  &__box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    margin-top: 160px;
    margin-bottom: 100px;

    @include tablet {
      margin-bottom: 40px;
    }
  }
  &__text {
    display: inline-block;
    margin: 0;
    width: 800px;
    font-weight: 400;
    font-size: 18px;
    line-height: 165%;
    max-width: 585px;
    letter-spacing: 0.05em;
    color: #fff;
    text-align: center;


    @include tablet {
      width: auto;
      max-width: 540px;
      letter-spacing: 0.2px;
    }


    @include mobile-final {
      margin-top: 12px;
      margin-bottom: 0;
      max-width: 100%;
      font-size: 16px;
      line-height: 1.5;
      letter-spacing: 0.1px;
    }
  }

  &__img {
    position: absolute;
    right: 37%;
    bottom: 17%;
    width: 335px;
    height: auto;
    opacity: .5;

    @include tablet {
      right: -13px;
      bottom: 10px;
      width: 321px;
      height: 323px;
    }

    @include pre-mobile {
      right: 0;
      width: 280px;
      height: auto;
    }

    @include mobile {
      left: 0;
      bottom: 75px;
      width: 370px;
    }

    @include mobile-final {
      position: static;
      inset: auto;
      margin-bottom: 16px;
      width: 100%;
    }
  }
}

.text-yel {
  font-family: var(--second-family);
  color: #f2e186;
}

.text-orang {
  font-family: var(--second-family);
  letter-spacing: -0.2px;
  color: #df7e29;
}

.text-shadow {
  color: var(--tekst-abzac);
}