.steps {
    padding-top: px;
    padding-bottom: 120px;

    @include tablet {
        padding-bottom: 80px;
    }

    @include mobile-final {
        padding-bottom: 64px;
    }

    &__title {
        margin-bottom: 32px;
        width: 100%;
        text-align: center;

        @include tablet {
            font-size: 32px;
        }

        @include mobile-final {
            margin-bottom: 23px;
            font-size: 24px;
            letter-spacing: -0.6px;
        }
    }
}

.steps-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
    margin-top: 100px;

    @include tablet {
        flex-direction: column;
        gap: 40px;
        margin-top: 50px;
    }


    @include mobile-final {
        gap: 20px;
    }
}

.step {
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    cursor: pointer;

    @include for-desktop {
        width: 430px;
        height: 268px;
        gap: 20px;
    }


    @include mobile-final {
        padding: 16px 15px;
        width: auto;
        height: auto;
        max-width: 100%;
    }

    &__title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 36px;
        line-height: 1.3;
        letter-spacing: -1.8px;
        color: var(--tekst-zagolovok);
        border-radius: 15px;
        border: 1px solid var(--osnovnoy-hover);
        text-align: center;

        @include for-desktop {
            font-size: 28px;
        }

        @include tablet {
            font-size: 24px;
            letter-spacing: -0.6px;
        }

        @include mobile-final {
            margin-bottom: 5px;
            font-size: 18px;
        }
    }

    &__num {
        margin-bottom: 110px;
        font-weight: 400;
        font-size: 14px;
        line-height: 125%;
        letter-spacing: 0.03em;
        color: var(--color---light-blue-100);

        @include for-desktop {
            margin-bottom: 60px;
        }

        @include tablet {
            margin-bottom: 0;
        }
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        height: 100%;
        gap: 20px;
    }

    &__img {
        height: 215px;
        border-radius: 15px;
        border: 1px solid var(--osnovnoy-hover);
    }
}

.step--colorTwo {
    margin-top: -50px;

    @include tablet {
        margin-top: 0;
    }
}
.step--colorFour{
    margin-top: -100px;

    @include tablet {
        margin-top: 0;
    }
}






