

// если вы хотите использовать sass-переменные - удалите root
body {
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.05em;
  color: var(--tekst-abzac);
  background: var(--fon-charcoal);
}

textarea {
  resize: none;
}

.section-title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 48px;
  line-height: 1.32;
  letter-spacing: -1.9px;
  color: var(--tekst-zagolovok);
  text-align: center;
}
:root {
  --font-family: "RFDewiExpanded", sans-serif;
  --second-family: "Monaspace Argon", sans-serif;
  --content-width: 1200px;
  --container-offset: 32px;
  --container-mobile: 32px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --fon-charcoal: #010101;
  --fon-dark-blue---kartochki: #0f172a;
  --osnovnoy-hover: #FFC300;
  --osnovnoy-emerald: #059669;
  --dopolnitelnyy-blue: #60a5fa;
  --dopolnitelnyy-linii: #1f2937;
  --akcentnyy-rose: #f43f5e;
  --tekst-zagolovok: #e5e7eb;
  --tekst-abzac: #9aa4b2;
  --color---light-blue-100: #60a5fa;
  --color---light-blue-12: rgba(96, 165, 250, 0.12);
  --color---green-100: #46b798;
  --color---green-12: rgba(70, 183, 152, 0.12);
  --color---white: #fff;
  --color---black: #010101;
  --color---dark-blue-100: #4a64ff;
  --color---dark-blue-12: rgba(74, 100, 255, 0.12);
  --color---cyan-100: #54ebfe;
  --color---cyan-12: rgba(84, 235, 254, 0.12);
  --color---red: #ff6370;
}
