.popup {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  overflow: hidden auto;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: 15px;
    padding: 32px;
    width: 100%;
    max-width: 646px;
    background: var(--fon-charcoal);

    @include small-tablet {
      margin: 0 20px;
      height: auto;
      gap: 20px;
    }

    @include mobile-final {
      padding: 16px;
      width: 100%;
      height: auto;
      max-width: 256px;
      gap: 10px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 30px;
    letter-spacing: -1px;
    text-align: center;
    color: var(--color---white);

    @include small-tablet {
      font-size: 18px;
      letter-spacing: 1px;
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--tekst-zagolovok);
    border-radius: 10px;
    padding: 14px 102px;
    height: 58px;
    font-size: 30px;
    background: var(--fon-charcoal);
    cursor: pointer;

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile-final {
      width: 100%;
    }
  }
}

.popup.open {
  opacity: 1;
  visibility: visible;
}
