.btn {
    position: relative;
    z-index: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border: none;
    border-radius: 35px;
    padding: 16px 105px 15px 80px;
    width: 461px;
    height: 65px;
    font-family: inherit;
    font-weight: 500;
    font-size: 30px;
    line-height: 150%;
    color: var(--tekst-zagolovok);
    background: transparent;
    cursor: pointer;
    margin: 0 auto;

    @include for-desktop-large {
        padding-right: 69px;
        padding-left: 54px;
        width: 337px;
    }

    @include tablet {
        display: flex;
        justify-content: space-between;
        padding-right: 35px;
        width: 295px;
        height: 57px;
        letter-spacing: -.9px;
    }

    @include mobile {
      margin-top: auto;
    }

    @include mobile-final {
        width: 100%;
    }

    @include mobile-final {
        justify-content: space-between;
        padding: 10px 23px;
        padding-right: 40px;
        height: 44px;
        font-size: 16px;
    }

    &__arrow {
        position: relative;
        z-index: 2;
        display: inline-block;
        margin-left: 70px;
        width: 24px;
        height: 24px;
        transform: translateX(-5px);
        transition: transform .3s ease-in;

        @include for-desktop-large {
            display: none;
            margin-left: 77px;
        }
    }

    &__text {
        position: relative;
        z-index: 2;
        font-weight: 500;
        font-size: 20px;
        line-height: 150%;
        color: var(--tekst-zagolovok);

        @include for-desktop-large {
            margin: 0 auto;
        }

        @include mobile-final {
            font-size: 16px;
            letter-spacing: -0.5px;
        }
    }

    &__effect {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        border-radius: 10px;
        width: 27%;
        height: 100%;
        background-color: var(--osnovnoy-hover);
        transition: width 0.4s ease;

        @include small-tablet  {
            width: 100%;
        }
    }

    @include hover {
        .btn__effect {
            width: 100%;
        }
    }

    @include hover {
        .btn__arrow {
            transform: translateX(0);
        }
    }
}
