.page-404 {
  position: relative;
  display: block;
  width: 100%;
  height: 100vh;
  background-image: url('../../img/intro-bg.png');
  background-size: cover;
  background-repeat: no-repeat;

  @include mobile-small {
    position: relative;
    display: block;
    width: 100%;
    height: 100vh;
    background-image: url('../../img/404-Mobile-bg.jpg');
    object-fit: cover;
  }

  &__wrapper {
    position: absolute;
    right: 160px;
    top: 50%;
    width: 388px;
    text-align: center;
    transform: translateY(-50%);

    @include for-desktop-large {
      right: 50px;
    }

    @include for-desktop {
      right: 70px;
      width: 240px;
    }

    @include mobile-small {
      position: static;
      margin-top: -150px;
      width: 225px;
    }
  }

  &__container {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 40px 30px 0;
    width: 100%;
    max-width: 1440px;

    @include for-desktop {
      padding: 32px;
    }

    @include mobile-small {
      flex-direction: column;
      padding: 16px;
    }
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 120px;
    line-height: 1;
    text-align: center;
    color: var(--color---white);

    @include for-desktop-large {
      font-size: 80px;
    }

    @include for-desktop {
      font-size: 40px;
    }

    @include mobile-small {
      font-size: 60px;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 40px;
    line-height: 150%;
    color: #b3ffc7;

    @include for-desktop-large {
      font-size: 30px;
    }

    @include for-desktop {
      font-size: 20px;
    }
  }

  &__img {
    display: block;
    width: auto;
    height: auto;
    background-size: cover;

    @include mobile-small {
      display: block;
      width: 100%;
      height: auto;
      max-width: 310px;
      background-size: cover;
    }
  }

  &__btn {
    display: inline-flex;
    margin-top: 30px;
    padding-right: 77px;
    padding-left: 46.5px;
    width: 346px;
    height: auto;

    @include for-desktop {
      margin: 0 auto 15px;
      padding-right: 24.5px;
      padding-left: 46.5px;
      width: 240px;
      height: 56px;
      font-size: 20px;
    }

    @include pre-mobile {
      padding-top: 10px;
      padding-bottom: 10px;
      width: 210px;
      height: auto;
      font-size: 14px;
    }

    @include mobile-small {
      margin-top: 20px;
      width: 100%;
    }
  }

  &__btn-effect {
    @include for-desktop {
      width: 100%;
    }
  }

  &__btn-text {
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    color: var(--tekst-zagolovok);

    @include for-desktop {
      font-size: 20px;
    }

    @include pre-mobile {
      font-size: 16px;
      line-height: 1;
    }
  }

  &__arrow {
    margin-left: 84px;

    @include for-desktop {
      margin-left: 0;
    }
  }
}
