.footer {
    position: relative;
    padding: 40px 0;
    width: 100%;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 1px;
    color: var(--tekst-abzac);
    background-size: cover;
    background-repeat: no-repeat;

    @include pre-mobile {
        font-size: 14px;
    }

    @include mobile-final {
        padding-top: 18.5px;
        padding-bottom: 32px;
        font-size: 16px;
    }


    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 8px;

        @include small-tablet {
            flex-direction: column;
        }
    }

    &__row {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        max-width: 320px;

        @include small-tablet {
            max-width: 100%;
            flex-direction: row;
            justify-content: space-between;
        }

        @include mobile {
            flex-direction: column;
            max-width: 100%;
        }


        @include mobile-final {
            flex-direction: column;
            gap: 2px;
        }
    }

    &__title {
        margin-bottom: 45px;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 32px;
        letter-spacing: -1px;
        color: var(--osnovnoy-hover);

        @include tablet {
            margin-bottom: 32px;
            font-size: 16px;
        }

        @include mobile-final {
            margin-bottom: 28px;
        }
    }

    &__item {
        margin-bottom: 10px;

        @include mobile-final {
            margin-bottom: 5px;
        }

        &-name {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: .2px;
            color: var(--tekst-abzac);

            @include tablet {
                font-size: 14px;
            }

            @include mobile {
                flex-direction: row;
                max-width: 100%;
            }

            @include mobile-final {
                flex-direction: column;
                font-size: 14px;
            }
        }

        &-contact {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 0.05em;
            color: var(--tekst-abzac);

            @include tablet {
                font-size: 16px;
            }
        }
    }

    &__item-icon {
        width: 13px;
        height: auto;
    }

    &__information {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        max-width: 260px;
        gap: 16px;

        @include tablet {
            flex-wrap: wrap;
            justify-content: flex-start;
            max-width: 209px;
            gap: 0;
        }

        @include small-tablet {
            max-width: 100%;
        }


        @include mobile-final {
            flex-direction: column;
            align-items: baseline;
            gap: 10px;
        }
    }

    &__userAgr {
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 0.05em;
        text-decoration: underline;
        color: var(--tekst-abzac);
        text-decoration-skip-ink: none;

        @include tablet {
            margin-bottom: 10px;
            max-width: 300px;
            font-size: 14px;
        }

        @include mobile-final {
            margin-bottom: 0;
            font-size: 14px;
            line-height: 125%;
            letter-spacing: 0.03em;
        }
    }

    &__copyright {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 1px;
        color: var(--tekst-abzac);

        @include tablet {
            margin-top: 16px;
            font-size: 14px;
        }

        @include mobile {
            margin-top: 0;
            width: 200px;
        }
    }

    &__developer {
        position: absolute;
        right: 20px;
        bottom: 10px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        letter-spacing: .05em;
        color: var(--osnovnoy-hover);

        @include mobile {
            font-size: 10px;
        }
    }

    &__developer-link {
        border-bottom: 1px solid var(--osnovnoy-hover);
        color: var(--osnovnoy-hover);
        transition: all .3s ease-in;

        &:hover {
            border-color: #fff;
            color: #fff;
        }
    }
}

.footer__logo {
    font-weight: 400;
    font-size: 24px;
    line-height: 160%;
    letter-spacing: -0.03em;
    opacity: 0.5;
    display: flex;
    align-items: center;
    text-align: center;
    gap: 2px;
    margin-bottom: 20px;
}
.footer__contacts {
    margin-bottom: 20px;
}
.text-wh {
    color: #fff;
}
